
import Config from '../config'

const langTypes = Config.langTypes;

const routerDocs = [
    'introduce',
    'install',
    'quick-start',
    'button',
    'icon',
    'layout',
    'menu',
    'badge',
    'input',
    'select',
    'checkbox',
    'radio',
    'switch',
    'slider',
    'upload',
    'popover',
    'tooltip',
    'tag',
    'modal',
    'message',
    'progress',
    'alter',
    'card',
    'drawer',
    'notice',
    'table',
    'collapse',
    'grid',
    'tabs',
    'page',
    'list',
    'loading',
    'back-top',
    'affix',
    'date-picker',
    'form',
    'dropdown',
    'time-picker',
    'color',
    'time',
    'image',
    'skeleton',
    'cascader',
    'tree',
];

let routers = [];

const renderRouter = () => {
    routerDocs.forEach(routerItem => {
        langTypes.forEach(langItem => {
            routers.push({
                path: `${langItem}/docs/${routerItem}`,
                component: () => import(`../views/docs/${langItem}/${routerItem}.md`)
            })
        })
    })
};
renderRouter();

export default routers
